import { useParams, useLocation } from "react-router";
import { useState, useEffect } from 'react';
import useFetch from "./useFetch";
import Image from 'material-ui-image';
import Carousel from 'react-material-ui-carousel'
import Button from "@material-ui/core/Button";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import { useHistory } from "react-router-dom"
import ReactStars from 'react-stars'
import './CommentDialog';
import CommentDialog from "./CommentDialog";
const ProductDetails = ({ handleCartLength }) => {
    const { id } = useParams();
    const { data: product, error, isPending } = useFetch('/data/products/' + id)
    const location = useLocation();
    let [count, setCount] = useState(1);
    const [user, setUser] = useState(null)
    const history = useHistory();
    const {
        data: uData,
        error: uError,
        isPending: uIsPending
    } = useFetch("/user")


    useEffect(() => {
        console.log(uData)
        setUser(uData)
    }, [uData])


    const findElement = (cart) => {
        let i = -1
        cart.forEach((element, index) => {
            if (JSON.stringify(element) === JSON.stringify(product)) {
                i = index
            }
        });
        return i;
    }

    const handleClick = async () => {

        if (user) {
            let cart = JSON.parse(sessionStorage.getItem('cart'))
            let counts = JSON.parse(sessionStorage.getItem('count'))
            console.log(cart, counts)
            if (cart) {
                let i = findElement(cart)
                console.log(i)
                if (i !== -1) {
                    counts[i] = parseInt(counts[i]) + count
                }
                else {
                    cart.push(product)
                    counts.push(count)
                    sessionStorage.setItem('cart', JSON.stringify(cart))
                }
                sessionStorage.setItem('count', JSON.stringify(counts))
            }
            else {
                cart = [product]
                counts = [count]
                sessionStorage.setItem('cart', JSON.stringify(cart))
                sessionStorage.setItem('count', JSON.stringify(counts))
            }

            handleCartLength(cart.length)
        }
        else {
            localStorage.setItem("location", location.pathname)
            history.push("/login")
            history.go()

        }
    }
    const handleCount = (isAdd) => {
        if (isAdd)
            count++;

        else {
            if (count > 1) {
                count--;
            }
        }
        setCount(count);
    }
    const ratingChanged = (newRating) => {
        console.log(newRating);
      };
    
      const [open, setOpen] = useState(false);

      let handleClickOpen = () => {
        setOpen(true);
      };
    
      let handleClose = () => {
        setOpen(false);
      };
      return (
        <div className="product-details" >
            {(isPending || uIsPending) && <div> Loading </div>}
            {(error || uError) && <div>{error}</div>}
            {product && (<div style ={{
                display: "flex", 
                flexWrap: "wrap",
                justifyContent: "center"}}>
                <div style={{flexGrow: 1}}>
                { <Carousel >
                    {product.images.map((img, i) =>
                        <Image key={i}
                            onClick={() => console.log('onClick')}
                            src={img}
                            aspectRatio={(4 / 3)} />)
                    }
                </Carousel>
                }
                {/* <ReactStars
                    count={5}
                    onChange={ratingChanged}
                    size={48}
                    isHalf={true}
                    emptyIcon={<i className="far fa-star"></i>}
                    halfIcon={<i className="fa fa-star-half-alt"></i>}
                    fullIcon={<i className="fa fa-star"></i>}
                    activeColor="#ffd700"
                 />, */}
                </div>
                <div style={{flexGrow: 1}}>
                <h2>{product.name}</h2>
                <div className="add-cart">
                    <ButtonGroup style={{ margin: "2%" }} size="small" aria-label="small outlined button group">
                        <Button onClick={() => handleCount(false)}>-</Button>
                        <Button style={{ backgroundColor: "white", color: "black" }} disabled>{count}</Button>
                        <Button onClick={() => handleCount(true)}>+</Button>
                    </ButtonGroup>
                    <button style={{ margin: "2%" }} onClick={handleClick}> Add to cart</button>
                </div>

                <p style={{ fontSize: "150%" }}> <b>  à§³ {product.price}  </b></p>
                <p style={{ marginTop: "1em" }}>{product.description}</p>
                
            </div>
         
            </div>
            )
            }
            <div style={{margin:"0 auto"}}>
                <button onClick={handleClickOpen} size="small">Add Comment</button>
            </div>           

             <CommentDialog open={open} handleClose={handleClose}/>
        </div >
    );
}

export default ProductDetails;